import React, { useState } from "react";

import "./style/Contact.css";
import emailjs from "emailjs-com";


function Contact() {
  const [data, setData] = useState(
    {
      Name:'',
      Email:'',
      Message:'',
      
    }
  );
    
 

  function handleChange(e) {
    const newdata = { ...data };
    console.log(newdata);
    newdata[e.target.name] = e.target.value;
    setData(newdata);
    console.log(newdata);
  }

  function handleSubmit(e){
  if(data.Name!==''&& data.Email!=='' && data.Message!==''){
    e.preventDefault()

     emailjs
      .sendForm(
      'service_fe0vegl',
       'template_w4hyoo2',
        e.target,
         'user_k2Y7xjpEMlaUopI3gv4ts'
      )
      .then((res) => {
        console.log(res);
      })
       .catch((err) => console.log(err));
    }
    else{
      console.log("Fill all the values in text field")
    }
      // alert("Thanks for sending message, Will get back to you soon")
      // e.target.reset();
     
  }


  const handleReset=()=>{

setData({
  Name:'',
  Email:'',
  Message:'',

})

  }
  return (
   
    <div class="bg-white">
       <div class="text-3xl py-6 text-center font-mono">Give Your Feedback</div>
      <br />
      <article class="flex flex-col justify-center px-6 py-3">

    
        <form onSubmit={handleSubmit} class="grid grid-cols-1 gap-4">
        <div className="form-group">
          {""}
          {""}

          <label class="font-mono">Name</label>
          <input
            name="Name"
            value={data.Name}
            class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
            onChange={(e) => handleChange(e)}
            placeholder="Enter Your Name..."
            required
          />

          {/* <label className="right-inline">Last Name</label>
  <input   className="input-control" /> */}
        </div>

        <div className="form-group">
          <label  class="font-mono">Your Email</label>
          <input
            name="Email"
            type="email"
            value={data.Email}
            placeholder="Enter Your Mail..."
            class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-6 px-6 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 "
            onChange={(e) => handleChange(e)}
            required
          />
        </div>

        <div className="form-group">
          <label  class="font-mono">Your Message</label>
          <textarea
            name="Message"
            value={data.Message}
            class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
            placeholder="Enter Your Message..."
            onChange={(e) => handleChange(e)}
            required
          ></textarea>
        </div>

        <div className="form-group">
          <label>&nbsp;</label>
           <button type="submit" value="Submit" className="contact-button" >Send</button> 
           <button onClick={handleReset} className="contact-reset" >Reset</button> 
          {/* <input type="submit" value="Submit" /> */}
        </div>
        </form>
      </article>
    
  


    </div>
  );
}

export default Contact;
